* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  background-color: rgb(41, 40, 40);
  
}
.main{
  background-color: black !important;
}
.startGame{
  display: flex;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.startGame h1{
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
}
.startGameButton{
  font-size: 18px;
  font-weight: 700;
}

.startGame, .quiz-content{
  background-image: url("../image/annie-spratt-zA7I5BtFbvw-unsplash.jpg");
  background-size: cover;
  background-position: center;

}
.quiz-content{
  width: 100%;
  height: 100%;
}

ul{
  list-style-type: none;
}
.quiz-content{
  padding: 25px 20px; 

}

.question{
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
  list-style: none; 
  margin: 25px, 75px;
  line-height: 19px;
}

.all-answers{
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  padding: 5px 10px;
  margin-right: 10px;
  margin-top: 5px;
  border-radius: 10px;
  border: none;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  
}
.all-answers:hover{
  cursor: pointer;
}
.button-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
button{
  margin: 10px 20px;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  font-size: 16px;
  font-weight: 500;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.line{
  color: black;
  line-height: 2px;
  margin: 15px 0;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .startGame, .quiz-content{
    width: 500px;
    height: 96vh;
    margin: 0 auto;
    margin-top: 15px;
  }
}

@media screen and (min-width: 1366px) and (max-width: 1920px) {
  .startGame, .quiz-content{
      width: 700px;
      height: 96vh;
      margin: 0 auto;
      margin-top: 15px;
    }
}
@media screen and (min-width: 2000px) and (max-width: 2560px) {
    .startGame, .quiz-content{
        width: 1500px;
        height: 90vh;
        margin: 100px auto;
    }
    
    .startGame h1{
      text-align: center;
      font-size: 80px;
      margin-bottom: 20px;
    }
    .startGameButton{
      font-size: 40px;
      font-weight: 700;
    }
    .question, .all-answers{
      font-size: 36px;
      font-weight: 900;
      line-height: 30px;
    }
    .question{
        margin: 30px 50px;
      }
      .all-answers{
        padding: 10px 25px;
        margin-left: 30px;
        margin-bottom: 10px;
      }
      button{
        font-size: 36px;
        font-weight: 900;
      }
  }